import React from 'react';
import { Coins } from 'lucide-react';
import ImageDisplay from './ImageDisplay';

const ActiveBets = ({ bets, getNumberColor }) => (
  <section className="bg-purple-900/40 rounded-xl sm:rounded-3xl p-4 sm:p-6 border border-purple-700/30 backdrop-blur-sm">
    <h2 className="text-lg sm:text-xl font-semibold text-transparent bg-clip-text bg-gradient-to-r from-pink-400 to-purple-500 mb-4">
      Active Bets
    </h2>
    <div className="flex flex-wrap gap-2 max-h-[150px] overflow-y-auto hide-scrollbar">
      {bets.map((bet, index) => (
        <div
          key={index}
          className={`py-1.5 px-2.5 rounded-lg flex items-center gap-1.5
            shadow-md hover:shadow-lg transition-all
            ${bet.type === 'NUMBER' ? getNumberColor(bet.number) : 'bg-purple-700'}
            text-white transform hover:scale-105 
            border border-purple-600/50
            text-sm`}
        >
          <Coins className="w-4 h-4" />
          <span className="font-medium">{bet.betAmount}</span>
          <span>on</span>
          {bet.type === 'NUMBER' ? (
            <div className="w-6 h-6 relative overflow-hidden rounded-md">
              <ImageDisplay id={bet.number} className="w-full h-full object-cover" />
            </div>
          ) : (
            <span className="font-medium">{bet.type}</span>
          )}
        </div>
      ))}
    </div>
  </section>
);

export default ActiveBets;