// src/State/Coins/Action.js

import { api } from "../../Config/Api";
import {
    ADD_COINS_REQUEST,
    ADD_COINS_SUCCESS,
    ADD_COINS_FAILURE,
    DEDUCT_COINS_REQUEST,
    DEDUCT_COINS_SUCCESS,
    DEDUCT_COINS_FAILURE,
    GET_COINS_REQUEST,
    GET_COINS_SUCCESS,
    GET_COINS_FAILURE,
} from "./ActionType";

// Add coins action
export const addCoins = (data) => async (dispatch) => {
    dispatch({ type: ADD_COINS_REQUEST });
    try {
        const response = await api.post('/api/coins/add', data, {
            headers: {
                Authorization: `Bearer${localStorage.getItem("jwt")}`
            }
        });
        dispatch({ type: ADD_COINS_SUCCESS, payload: response.data });
    } catch (error) {
        dispatch({ type: ADD_COINS_FAILURE, payload: error.message });
    }
};

// Deduct coins action
export const deductCoins = (data) => async (dispatch) => {
    console.log(data);
    dispatch({ type: DEDUCT_COINS_REQUEST });
    try {
        const response = await api.post('/api/coins/deduct', data, {
            headers: {
                Authorization: `Bearer${localStorage.getItem("jwt")}`
            }
        });
        dispatch({ type: DEDUCT_COINS_SUCCESS, payload: response.data });
    } catch (error) {
        dispatch({ type: DEDUCT_COINS_FAILURE, payload: error.message });
    }
};

// Get coins balance action
export const getCoinsBalance = (email) => async (dispatch) => {
    dispatch({ type: GET_COINS_REQUEST });
    try {
        const response = await api.get(`/api/coins/balance/${email}`, {
            headers: {
                Authorization: `Bearer${localStorage.getItem("jwt")}`
            }
        });
        dispatch({ type: GET_COINS_SUCCESS, payload: response.data });
    } catch (error) {
        dispatch({ type: GET_COINS_FAILURE, payload: error.message });
    }
};