import {
    CREATE_GAME_REQUEST,
    CREATE_GAME_SUCCESS,
    CREATE_GAME_FAILURE,
    PLACE_BET_REQUEST,
    PLACE_BET_SUCCESS,
    PLACE_BET_FAILURE,
    SPIN_WHEEL_REQUEST, 
    SPIN_WHEEL_SUCCESS,
    SPIN_WHEEL_FAILURE,
    GET_GAME_STATUS_REQUEST,
    GET_GAME_STATUS_SUCCESS,
    GET_GAME_STATUS_FAILURE
} from "./ActionType";

const initialState = {
    currentGame: null,
    loading: false,
    error: null,
    gameHistory: [],
    success: false
};

export const SoratReducer = (state = initialState, action) => {
    switch (action.type) {
        case CREATE_GAME_REQUEST:
        case PLACE_BET_REQUEST:
        case SPIN_WHEEL_REQUEST:
        case GET_GAME_STATUS_REQUEST:
            return {
                ...state,
                loading: true,
                error: null,
                success: false
            };

        case CREATE_GAME_SUCCESS:
            return {
                ...state,
                loading: false,
                currentGame: action.payload,
                success: true
            };

        case PLACE_BET_SUCCESS:
            return {
                ...state,
                loading: false,
                success: true
            };

        case SPIN_WHEEL_SUCCESS:
        case GET_GAME_STATUS_SUCCESS:
            return {
                ...state,
                loading: false,
                currentGame: action.payload,
                success: true
            };

        case CREATE_GAME_FAILURE:
        case PLACE_BET_FAILURE:
        case SPIN_WHEEL_FAILURE:
        case GET_GAME_STATUS_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.payload,
                success: false
            };

        default:
            return state;
    }
};