import { Box, Button, Divider, IconButton, TextField, Typography } from '@mui/material';
import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { Field, Form, Formik } from 'formik';
import { deductCoins } from '../State/Coin/Action';
import * as Yup from 'yup';

const initialValue = {
    coin: ""
}

const WithdrawCoins = () => {
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const [coin, setCoin] = useState(0);
    const { auth } = useSelector(store => store);

    const handleTextChange = (event) => {
        setCoin(event.target.value);
    };

    return (
        <div className="min-h-[80vh] w-full">
            {/* Header with back button */}
            <div className='w-full p-8 flex items-center'>
                <IconButton onClick={() => navigate("/account/profile")}>
                    <ArrowBackIcon />
                </IconButton>
                <Typography variant='h5' className='ml-4'>
                    Withdraw Coins
                </Typography>
            </div>

            {/* Main content */}
            <Box className='w-full max-w-md mx-auto px-4'>
                <Formik
                    initialValues={initialValue}
                    validationSchema={Yup.object({
                        coin: Yup.number()
                            .typeError('Must be a number')
                            .min(1000, 'Must be greater than 1000')
                            .required('Required')
                    })}
                    validateOnBlur={false}
                    validateOnChange={false}
                >
                    {({ errors }) => (
                        <Form>
                            <Box sx={{ display: "flex", flexDirection: "column", gap: 2 }}>
                                <Field
                                    as={TextField}
                                    name="coin"
                                    label="Coins"
                                    value={coin}
                                    onChange={handleTextChange}
                                    fullWidth
                                    variant="outlined"
                                    margin="normal"
                                    error={!!errors.coin}
                                    helperText={errors.coin}
                                />
                                
                                <Divider className="my-4" />
                                
                                {/* Bill Details Section */}
                                <div className="space-y-4">
                                    <Typography variant="subtitle1" className="font-light">
                                        Bill Details
                                    </Typography>
                                    
                                    <div className="space-y-3">
                                        <div className="flex justify-between text-gray-400">
                                            <Typography>Withdrawal Amount</Typography>
                                            <Typography>₹{(coin/10).toFixed(3)}</Typography>
                                        </div>
                                    </div>
                                </div>

                                <Button 
                                    fullWidth 
                                    onClick={() => dispatch(deductCoins({
                                        email: auth.user?.email,
                                        coins: coin
                                    }))} 
                                    variant='contained'
                                    sx={{ 
                                        mt: 4,
                                        padding: "1rem",
                                        textTransform: "none",
                                        fontSize: "1rem"
                                    }}
                                >
                                    Withdraw
                                </Button>
                            </Box>
                        </Form>
                    )}
                </Formik>
            </Box>
        </div>
    )
}

export default WithdrawCoins