import React from 'react'
import { Route, Routes } from 'react-router-dom'
import Home from '../Component/Home/Home'
import ChessEntry from '../Component/Game/ChessEntry'
import LoginForm from '../Component/Auth/LoginForm'
import RegisterForm from '../Component/Auth/RegisterForm'
import Profile from '../Component/Profile/Profile'
import UserProfile from '../Component/Profile/UserProfile'
import RouletteCell from '../Component/Game/Roulette/Roulette'
import MainRoulette from '../Component/Game/Roulette/Roulette'
import { AudioProvider } from '../Component/Game/Roulette/AudioProvider'
const Routers = () => {
  return (
    <>
        <Routes>
        
            <Route path='/' element={<Home/>}/>
            <Route path='/account/profile/*' element={<Profile/>}/>
            <Route path='/my-profile/*' element={<Profile/>}/>
            <Route path='/account/register' element={<RegisterForm/>}/>
            <Route path='/account/login' element={<LoginForm/>}/>
            <Route path='/game/chess' element={<ChessEntry/>}/>
            <Route path='/account/profile/*' element={<UserProfile/>}/>
            <Route path="/game/sorat" element={
    <AudioProvider>
      <MainRoulette />
    </AudioProvider>
  } />

        </Routes>
    </>
  )
}

export default Routers