export const getNumberColor = (num) => {
    const redNumbers = [1, 3, 5, 7, 9, 11];
    return redNumbers.includes(num) ? 'bg-rose-600' : 'bg-slate-800';
  };
  
  export const getPhaseText = (gamePhase, timeLeft, isSpinning) => {
    switch (gamePhase) {
      case 'betting':
        return `Betting Phase - ${timeLeft}s remaining`;
      case 'spinning':
        return isSpinning ? 'Spinning...' : 'Preparing to spin...';
      case 'result':
        return `Next game in ${timeLeft}s`;
      default:
        return '';
    }
  };