import { Button, TextField, Typography } from '@mui/material';
import { Field, Form, Formik } from 'formik';
import React from 'react';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { registerUser } from '../State/Authentication/Action';
import * as Yup from 'yup';

const initialValue = {
    name: "",
    email: "",
    password: "",
    liChessId: ""
};

const validationSchema = Yup.object({
    name: Yup.string().required("Full Name is required"),
    email: Yup.string().email("Invalid email format").required("Email is required"),
    password: Yup.string().min(6, "Password must be at least 6 characters").required("Password is required"),
    liChessId: Yup.string().required("LiChess Username is required")
});

const RegisterForm = () => {
    const navigate = useNavigate();
    const dispatch = useDispatch();

    const handleSubmit = (values) => {
        const { name, email, password, liChessId } = values;
        const userData = { name, email, password, liChessId };
        console.log('fullName:', name);
        console.log('liChessId:',liChessId)
        dispatch(registerUser({ userData, navigate }));
    };

    return (
        <div>
            <Typography variant='h5' className='text-center'>
                Register
            </Typography>

            <Formik 
                onSubmit={handleSubmit} 
                initialValues={initialValue} 
                validationSchema={validationSchema}
            >
                {({ errors, touched }) => (
                    <Form className='gap-2'>
                    <Field
                            as={TextField}
                            name="name"
                            label="Full Name"
                            fullWidth
                            variant="outlined"
                            margin="normal"
                            error={touched.name && Boolean(errors.name)}
                            helperText={touched.name && errors.name}
                        />

                        <Field
                            as={TextField}
                            name="liChessId"
                            label="liChess Username"
                            fullWidth
                            variant="outlined"
                            margin="normal"
                            error={touched.username && Boolean(errors.username)}
                            helperText={touched.username && errors.username}
                        />
                        <Typography variant='body2' align='center' sx={{ mt: 3 }}>
                            Dont have a LiChess Account?
                            <Button size='small' variant="text" onClick={() => window.open('https://lichess.org/signup')}>Sign Up</Button>
                        </Typography>
                        <Field
                            as={TextField}
                            name="email"
                            label="Email"
                            fullWidth
                            variant="outlined"
                            margin="normal"
                            error={touched.email && Boolean(errors.email)}
                            helperText={touched.email && errors.email}
                        />

                        <Field
                            as={TextField}
                            name="password"
                            label="Password"
                            fullWidth
                            variant="outlined"
                            margin="normal"
                            type="password"
                            error={touched.password && Boolean(errors.password)}
                            helperText={touched.password && errors.password}
                        />
                        <Button fullWidth type='submit' variant='contained' sx={{ mt: 2, padding: "1rem" }}>Register</Button>
                        <Typography variant='body2' align='center' sx={{ mt: 3 }}>
                            Already have an account?
                            <Button size='small' variant="text" onClick={() => navigate('/account/login')}>Login</Button>
                        </Typography>
                    </Form>
                )}
            </Formik>
        </div>
    );
};

export default RegisterForm;