// src/State/Coins/Reducer.js

import {
    ADD_COINS_REQUEST,
    ADD_COINS_SUCCESS,
    ADD_COINS_FAILURE,
    DEDUCT_COINS_REQUEST,
    DEDUCT_COINS_SUCCESS,
    DEDUCT_COINS_FAILURE,
    GET_COINS_REQUEST,
    GET_COINS_SUCCESS,
    GET_COINS_FAILURE,
} from "./ActionType";

const initialState = {
    coins: 0,
    loading: false,
    error: null,
    message: null,
    success: false
};

export const coinReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_COINS_REQUEST:
        case DEDUCT_COINS_REQUEST:
        case GET_COINS_REQUEST:
            return {
                ...state,
                loading: true,
                error: null,
                success: false
            };

        case ADD_COINS_SUCCESS:
        case DEDUCT_COINS_SUCCESS:
            return {
                ...state,
                loading: false,
                coins: action.payload.updatedCoins,
                message: action.payload.message,
                success: true
            };

        case GET_COINS_SUCCESS:
            return {
                ...state,
                loading: false,
                coins: action.payload,
                success: true
            };

        case ADD_COINS_FAILURE:
        case DEDUCT_COINS_FAILURE:
        case GET_COINS_FAILURE:
            return {
                ...state,
                loading: false,
                error: action.payload,
                success: false
            };

        default:
            return state;
    }
};