import { api } from "../../Config/Api";
import {
    CREATE_GAME_REQUEST,
    CREATE_GAME_SUCCESS,
    CREATE_GAME_FAILURE,
    PLACE_BET_REQUEST,
    PLACE_BET_SUCCESS,
    PLACE_BET_FAILURE,
    SPIN_WHEEL_REQUEST, 
    SPIN_WHEEL_SUCCESS,
    SPIN_WHEEL_FAILURE,
    GET_GAME_STATUS_REQUEST,
    GET_GAME_STATUS_SUCCESS,
    GET_GAME_STATUS_FAILURE
} from "./ActionType";

export const createGame = () => async (dispatch) => {
    dispatch({ type: CREATE_GAME_REQUEST });
    try {
        const response = await api.post('/api/sorat/game/create', null, {
            headers: {
                Authorization: `Bearer${localStorage.getItem("jwt")}`
            }
        });
        dispatch({ type: CREATE_GAME_SUCCESS, payload: response.data });
        return response.data;
    } catch (error) {
        dispatch({ type: CREATE_GAME_FAILURE, payload: error.message });
        throw error;
    }
};

export const placeBet = (data) => async (dispatch) => {
    console.log("Received data in placeBet:", data); // Debug log
    
    // Validate required fields
    if (!data || !data.bet || !Array.isArray(data.bet)) {
        console.error("Invalid bet data:", data);
        throw new Error('Invalid bet data structure');
    }

    dispatch({ type: PLACE_BET_REQUEST });
    
    try {
        // Ensure bet data is properly structured
        const payload = {
            bet: data.bet,
            gameId: data.gameId,
            email: data.email
        };

        console.log("Sending payload to API:", payload); // Debug log

        const response = await api.post('/api/sorat/bet', payload, {
            headers: {
                Authorization: `Bearer${localStorage.getItem("jwt")}`
            }
        });
        
        dispatch({ type: PLACE_BET_SUCCESS, payload: response.data });
        return response.data;
    } catch (error) {
        console.error("Bet placement error:", error);
        dispatch({ type: PLACE_BET_FAILURE, payload: error.message });
        throw error;
    }
};




export const spinWheel = (gameId) => async (dispatch) => {
    dispatch({ type: SPIN_WHEEL_REQUEST });
    try {
        const response = await api.post(`/api/sorat/spin/${gameId}`, null, {
            headers: {
                Authorization: `Bearer${localStorage.getItem("jwt")}`
            }
        });
        dispatch({ type: SPIN_WHEEL_SUCCESS, payload: response.data });
        return response.data;
    } catch (error) {
        dispatch({ type: SPIN_WHEEL_FAILURE, payload: error.message });
        console.log("error",error)
        throw error;
    }
};

export const getGameStatus = (gameId) => async (dispatch) => {
    dispatch({ type: GET_GAME_STATUS_REQUEST });
    try {
        const response = await api.get(`/api/sorat/game/${gameId}`, {
            headers: {
                Authorization: `Bearer${localStorage.getItem("jwt")}`
            }
        });
        dispatch({ type: GET_GAME_STATUS_SUCCESS, payload: response.data });
        return response.data;
    } catch (error) {
        dispatch({ type: GET_GAME_STATUS_FAILURE, payload: error.message });
        throw error;
    }
};