import React, { useEffect, useRef } from "react";
import IconButton from '@mui/material/IconButton';
import Avatar from '@mui/material/Avatar';
import "./NavBar.css";
import { pink } from "@mui/material/colors";
import { Person } from "@mui/icons-material";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import CurrencyExchangeIcon from '@mui/icons-material/CurrencyExchange';
import { GetUser } from "../State/Authentication/Action";
import { getCoinsBalance } from "../State/Coin/Action";

export const NavBar = () => {
    const { auth, coins } = useSelector(store => store);
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const audioRef = useRef(0);

    // Effect for initial user data and authentication
    useEffect(() => {
        if (auth.jwt) {
            dispatch(GetUser(auth.jwt));
        }
    }, [auth.jwt, dispatch]);

    // Separate effect for coins balance updates
    useEffect(() => {
        const updateCoins = () => {
            if (auth?.user?.email) {
                dispatch(getCoinsBalance(auth.user.email));
            }
        };

        // Initial coins fetch
        updateCoins();

        // Set up an interval to periodically check for coin updates
        const coinUpdateInterval = setInterval(updateCoins, 5000); // Check every 5 seconds

        // Cleanup interval on component unmount
        return () => clearInterval(coinUpdateInterval);
    }, [auth?.user?.email, dispatch]);

    const handleAvatarClick = () => {
        if (!auth.user) {
            navigate("/account/login");
            return;
        }
        navigate("/account/profile");
    };

    return (
        <div className="px-5 z-50 sticky top-0 py-[.8rem] bg-[#e91e63] lg:px-20 flex justify-between">
            
    <audio ref={audioRef} src="public\sounds\background-music.mp3" loop />

            <div className="lg:mr-10 cursor-pointer flex items-center space-x-4">
                <li onClick={() => navigate("/")} className="logo font-semibold text-gray-300 text-2xl">
                    Cibus
                </li>
            </div>
            <div className="flex items-center space-x-2 lg:space-x-10">
                <div className="flex items-center space-x-2">
                    <IconButton>
                        <CurrencyExchangeIcon />
                        {coins.coins}
                    </IconButton>
                </div>
                <div className="">
                    {auth.user ? (
                        <Avatar 
                            onClick={handleAvatarClick} 
                            sx={{ bgcolor: "white", color: pink.A400 }}
                        >
                            {auth.user.name[0].toUpperCase()}
                        </Avatar>
                    ) : (
                        <IconButton onClick={() => navigate("/account/login")}>
                            <Person />
                        </IconButton>
                    )}
                </div>
            </div>
        </div>
    );
};

export default NavBar;