import { ENTER_TOURNAMENT_FAILURE, ENTER_TOURNAMENT_REQUEST, ENTER_TOURNAMENT_SUCCESS, GET_TOURNAMENTS_FAILURE, GET_TOURNAMENTS_REQUEST, GET_TOURNAMENTS_SUCCESS } from "./ActionType";

export const tournamentReducer = (state = {}, action) => {
    switch(action.type){
        case GET_TOURNAMENTS_REQUEST:
        case ENTER_TOURNAMENT_REQUEST:
            return {...state,loading:true,error:null,success:false}
        case GET_TOURNAMENTS_SUCCESS:
            return{
                ...state,
                loading:false,
                tournaments:action.payload,
                success:true
            }
        case ENTER_TOURNAMENT_SUCCESS:
            console.log("rnter tournament success",action.payload);
            return{
                ...state,
                loading:false,
                tournament:action.payload,
                message:action.payload.message,
                success:true
            }
        case GET_TOURNAMENTS_FAILURE:
        case ENTER_TOURNAMENT_FAILURE:
            return{
                ...state,
                loading:false,
                error:action.payload,
                success:false
            }
        default:
            return state
    }
};