import React, { useState } from 'react';
import { Info, X } from 'lucide-react';
import { Button, Dialog, DialogContent } from '@mui/material';

export const GameRulesModal = () => {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <>
      <Button
        variant="ghost"
        size="icon"
        className="bottom-4 left-4 bg-purple-900/60 text-white rounded-full w-12 h-12 z-50"
        onClick={() => setIsOpen(true)}
      >
        <Info className="w-6 h-6" />
      </Button>

      <Dialog open={isOpen} onOpenChange={setIsOpen}>
        <DialogContent className="bg-purple-900/95 text-white border-purple-700 p-6 rounded-xl max-w-[90vw] mx-4">
          <div className="flex justify-between items-center mb-4">
            <h3 className="text-xl font-semibold text-transparent bg-clip-text bg-gradient-to-r from-pink-400 to-purple-500">
              Game Rules
            </h3>
            <Button
              variant="ghost"
              size="icon"
              className="text-white"
              onClick={() => setIsOpen(false)}
            >
              <X className="w-5 h-5" />
            </Button>
          </div>
          <ul className="space-y-3 text-purple-200">
            <li className="flex items-center gap-2">
              <span className="w-2 h-2 bg-pink-400 rounded-full" />
              Select a picture to bet on
            </li>
            <li className="flex items-center gap-2">
              <span className="w-2 h-2 bg-pink-400 rounded-full" />
              Choose your bet amount
            </li>
            <li className="flex items-center gap-2">
              <span className="w-2 h-2 bg-pink-400 rounded-full" />
              Wait for the wheel to spin
            </li>
            <li className="flex items-center gap-2">
              <span className="w-2 h-2 bg-pink-400 rounded-full" />
              Win if your picture is selected!
            </li>
          </ul>
        </DialogContent>
      </Dialog>
    </>
  );
};