import React from 'react';
import { UseOrientation } from './UseOrientation';
import { BetCard } from './BetCard';
import { BettingControls } from './BettingControls';
import RouletteCell from './RouletteCell';
import { images } from './constants';

export const BettingSection = ({ 
  betType, 
  setBetType, 
  gamePhase,
  betAmounts,
  onBet,
  selectedNumber,
  setSelectedNumber,
  spinningIndex,
  getNumberColor 
}) => {
  const orientation = UseOrientation();
  const isPortrait = !orientation.includes('landscape');

  const betControlsContent = (
    <div className="bg-purple-900/40 rounded-xl p-4 border border-purple-700/30 backdrop-blur-sm h-full flex flex-col gap-4">
      <h3 className="text-lg font-semibold text-transparent bg-clip-text bg-gradient-to-r from-pink-400 to-purple-500">
        Place Your Bets
      </h3>
      <div className="flex-1 flex flex-col gap-3">
        <BettingControls
          betType={betType}
          setBetType={setBetType}
          gamePhase={gamePhase}
        />
        <div className={`grid ${isPortrait ? 'grid-cols-3' : 'grid-cols-1'} gap-2`}>
          {betAmounts.map((amount) => (
            <BetCard
              key={amount.value}
              amount={amount}
              gamePhase={gamePhase}
              onBet={onBet}
            />
          ))}
        </div>
      </div>
    </div>
  );

  const rouletteGrid = (
    <div className={`grid ${isPortrait ? 'grid-cols-6' : 'grid-cols-3 lg:grid-cols-6'} gap-2 sm:gap-3`}>
      {images.map((item) => (
        <RouletteCell 
          key={item.id}
          item={item}
          isSpinning={spinningIndex === item.id}
          isSelected={selectedNumber === item.id}
          gamePhase={gamePhase}
          onSelect={setSelectedNumber}
          getNumberColor={getNumberColor}
          className={`aspect-square w-full h-full ${isPortrait ? 'max-w-[80px]' : 'max-w-[100px]'} sm:max-w-none`}
        />
      ))}
    </div>
  );

  return isPortrait ? (
    <div className="space-y-4">
      {rouletteGrid}
      {betControlsContent}
    </div>
  ) : (
    <div className="flex gap-4">
      <div className="w-2/3">{rouletteGrid}</div>
      <div className="w-1/3">{betControlsContent}</div>
    </div>
  );
};

export default BettingSection;