import React from 'react';
import ImageDisplay from './ImageDisplay';

const RouletteCell = ({ 
  item, 
  isSpinning, 
  isSelected, 
  gamePhase, 
  onSelect, 
  getNumberColor, 
  className 
}) => {
  const isDisabled = gamePhase !== 'betting';
  const cellClasses = `
    ${className}
    ${getNumberColor(item.id)}
    ${isSpinning ? 'glow-animation' : ''}
    ${isSelected ? 'ring-4 ring-pink-500/50' : ''}
    rounded-lg p-1.5 transform hover:scale-105 transition-all
    ${isDisabled ? 'opacity-50 cursor-not-allowed' : 'cursor-pointer'}
  `;

  return (
    <button
      onClick={() => !isDisabled && onSelect(item.id)}
      disabled={isDisabled}
      className={cellClasses}
    >
      <div className="w-full h-full relative">
        <ImageDisplay 
          id={item.id} 
          className="w-full h-full object-contain rounded-lg"
        />
      </div>
    </button>
  );
};

export default RouletteCell;