import React, { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Alert } from '@mui/material';
import { Sparkles, PlayCircle } from 'lucide-react';
import Confetti from 'react-confetti';

import { createGame, placeBet, spinWheel } from '../../State/Roulette/Action';
import { AudioProvider, useAudio } from './AudioProvider';
import RouletteCell from './RouletteCell';
import BetCard from './BetCard';
import RecentWinners from './RecentWinners';
import ActiveBets from './ActiveBets';
import GameRules from './GameRules';
import BettingControls from './BettingControls';
import { images, betAmounts } from './constants';
import { getNumberColor, getPhaseText } from './utils';
import ImageDisplay from './ImageDisplay';
import { GameRulesModal } from './GameRulesModal';
import { BettingSection } from './BettingSection';
import GameTimer from './GameTimer';

const MainRoulette = () => {
  const [betType, setBetType] = useState('NUMBER');
  const [selectedNumber, setSelectedNumber] = useState(null);
  const [error, setError] = useState(null);
  const [bets, setBets] = useState([]);
  const [result, setResult] = useState(null);
  const [pastWinning, setPastWinning] = useState(() => {
    const saved = localStorage.getItem('roulettePastWinnings');
    return saved ? JSON.parse(saved) : [];
  });
  const [gamePhase, setGamePhase] = useState('betting');
  const [timeLeft, setTimeLeft] = useState(20);
  const [spinningIndex, setSpinningIndex] = useState(null);
  const [isSpinning, setIsSpinning] = useState(false);
  const [showConfetti, setShowConfetti] = useState(false);
  const spinIntervalRef = useRef(null);

  const SPIN_ANIMATION_DURATION = 3500;
  const RESULT_DISPLAY_DURATION = 2000;

  const { auth } = useSelector(store => store);
  const { currentGame } = useSelector(store => store.roulette);
  const dispatch = useDispatch();
  const { isMusicPlaying, toggleMusic, autoplayBlocked, winAudioRef, betAudioRef } = useAudio();

  const handleGameError = async (error) => {
    const errorMessage = error.message?.toLowerCase() || '';
    if (errorMessage.includes('game is already completed') || errorMessage.includes('game not found')) {
      try {
        await dispatch(createGame());
        setError('Starting new game...');
        setGamePhase('betting');
        setTimeLeft(20);
        setBets([]);
        setSelectedNumber(null);
        setResult(null);
        setSpinningIndex(null);
        setTimeout(() => setError(null), 2000);
      } catch (newGameError) {
        setError(newGameError.message || 'Failed to create new game');
      }
    } else {
      setError(error.message || 'An error occurred');
    }
  };

  const startSpinningAnimation = (finalNumber) => {
    let currentIndex = 0;
    const spinDuration = SPIN_ANIMATION_DURATION - 500;
    const initialInterval = 100;
    const finalInterval = 300;
    let currentInterval = initialInterval;
    const startTime = Date.now();

    const spin = () => {
      const elapsedTime = Date.now() - startTime;
      const progress = Math.min(elapsedTime / spinDuration, 1);
      currentInterval = initialInterval + (finalInterval - initialInterval) * progress;
      
      setSpinningIndex(currentIndex);
      currentIndex = (currentIndex + 1) % images.length;
      
      if (elapsedTime >= spinDuration - finalInterval) {
        clearInterval(spinIntervalRef.current);
        const stepsToWinner = (finalNumber - currentIndex + images.length) % images.length;
        
        if (stepsToWinner === 0) {
          setSpinningIndex(finalNumber);
        } else {
          let finalSteps = 0;
          const finalSequence = setInterval(() => {
            currentIndex = (currentIndex + 1) % images.length;
            setSpinningIndex(currentIndex);
            finalSteps++;
            if (finalSteps === stepsToWinner) {
              clearInterval(finalSequence);
              setSpinningIndex(finalNumber);
            }
          }, finalInterval);
        }
      }
    };

    spinIntervalRef.current = setInterval(spin, currentInterval);
  };

  const triggerWinEffects = () => {
    setShowConfetti(true);
    winAudioRef.current?.play();
    setTimeout(() => setShowConfetti(false), 3000);
  };

  const handleBet = async (betAmount) => {
    if (gamePhase !== 'betting') {
      setError('Betting phase has ended');
      return;
    }

    try {
      if (betType === 'NUMBER' && selectedNumber === null) {
        setError('Please select a picture first');
        return;
      }

      const betData = {
        bet: [{
          betType: betType,
          betAmount: betAmount,
          number: selectedNumber
        }],
        gameId: currentGame?.id,
        email: auth.user?.email
      };

      try {
        await dispatch(placeBet(betData));
        betAudioRef.current?.play();
        setBets(prev => [...prev, {
          betAmount: betAmount,
          number: selectedNumber,
          type: betType
        }]);
        
        if (betType === 'NUMBER') {
          setSelectedNumber(null);
        }
        setError(null);
      } catch (betError) {
        await handleGameError(betError);
        if (betError.message?.toLowerCase().includes('game is already completed') ||
            betError.message?.toLowerCase().includes('game not found')) {
          setTimeout(async () => {
            try {
              betData.gameId = currentGame?.id;
              await dispatch(placeBet(betData));
              betAudioRef.current?.play();
              setBets(prev => [...prev, {
                betAmount: betAmount,
                number: selectedNumber,
                type: betType
              }]);
              
              if (betType === 'NUMBER') {
                setSelectedNumber(null);
              }
              setError(null);
            } catch (retryError) {
              setError(retryError.message || 'Failed to place bet');
            }
          }, 1000);
        }
      }
    } catch (err) {
      setError(err.message);
    }
  };

  useEffect(() => {
    const initializeGame = async () => {
      try {
        await dispatch(createGame());
        setError(null);
      } catch (err) {
        handleGameError(err);
      }
    };

    const gameLoop = async () => {
      if (gamePhase === 'betting' && timeLeft === 0) {
        setGamePhase('spinning');
        setTimeLeft(Math.ceil((SPIN_ANIMATION_DURATION + RESULT_DISPLAY_DURATION) / 1000));
      } else if (gamePhase === 'spinning' && timeLeft === 0 && !isSpinning) {
        try {
          setIsSpinning(true);
          const response = await dispatch(spinWheel(currentGame.id));
          const winningNumber = response.winningNumber;
          
          startSpinningAnimation(winningNumber);
          
          setTimeout(() => {
            setResult(winningNumber);
            const playerWon = bets.some(bet => bet.number === winningNumber);
            if (playerWon) {
              triggerWinEffects();
            }
            
            const updatedWinnings = [winningNumber, ...pastWinning].slice(0, 20);
            setPastWinning(updatedWinnings);
            localStorage.setItem('roulettePastWinnings', JSON.stringify(updatedWinnings));
            
            setTimeout(() => {
              setGamePhase('result');
              setTimeLeft(3);
              setIsSpinning(false);
            }, RESULT_DISPLAY_DURATION);
          }, SPIN_ANIMATION_DURATION);
        } catch (err) {
          setIsSpinning(false);
          handleGameError(err);
        }
      } else if (gamePhase === 'result' && timeLeft === 0) {
        await initializeGame();
        setGamePhase('betting');
        setTimeLeft(20);
        setResult(null);
        setSpinningIndex(null);
      }
    };

    const timer = setInterval(() => {
      setTimeLeft(prev => Math.max(0, prev - 1));
      if (timeLeft === 0) {
        gameLoop();
      }
    }, 1000);

    if (!currentGame) {
      initializeGame();
    }

    return () => clearInterval(timer);
  }, [dispatch, timeLeft, gamePhase, currentGame, pastWinning, isSpinning]);

  useEffect(() => {
    return () => {
      if (spinIntervalRef.current) {
        clearInterval(spinIntervalRef.current);
      }
    };
  }, []);

  useEffect(() => {
    if (gamePhase === 'result') {
      setBets([]);
    }
  }, [gamePhase]);

  return (
    <div className="min-h-screen bg-gradient-to-br from-[#4A0E4E] via-[#2C0D2E] to-[#1A0A1C] py-4 px-2 sm:py-12 sm:px-4 font-sans">
      <div className="relative min-h-screen">
        {showConfetti && (
          <Confetti
            width={window.innerWidth}
            height={window.innerHeight}
            numberOfPieces={200}
            recycle={false}
            colors={['#ff00ff', '#00ffff', '#ffff00', '#ff0000']}
          />
        )}

        <button
          onClick={toggleMusic}
          className="fixed bottom-4 right-4 bg-purple-900/60 hover:bg-purple-800/60 text-white px-4 py-2 rounded-full backdrop-blur-sm border border-purple-700/30 transition-all duration-300 hover:scale-105 flex items-center gap-2"
        >
          {isMusicPlaying ? (
            <>
              <span className="animate-pulse">🔊</span> Mute Music
            </>
          ) : (
            <>
              🔇 Unmute Music
              {autoplayBlocked && <span className="text-xs ml-1">(Click to play)</span>}
            </>
          )}
        </button>

        <GameRulesModal />
        <GameTimer gamePhase={gamePhase} timeLeft={timeLeft} isSpinning={isSpinning}/>
        <div className="container mx-auto max-w-7xl">
          <div className="grid grid-cols-1 lg:grid-cols-12 gap-4 lg:gap-6">
            <div className="col-span-1 lg:col-span-8 bg-gradient-to-br from-purple-900/60 to-pink-900/60 rounded-xl lg:rounded-3xl backdrop-blur-xl border border-purple-800/30 shadow-2xl overflow-hidden">
              <div className="px-3 py-4 sm:px-6 sm:py-5 bg-gradient-to-r from-purple-900/50 to-pink-900/50 border-b border-purple-700/30">
                <div className="flex flex-col sm:flex-row gap-3 sm:gap-0 sm:justify-between items-center">
                  <div className="flex items-center gap-3">
                    <div className="bg-gradient-to-br from-pink-500 to-purple-600 p-2 sm:p-2.5 rounded-full shadow-lg">
                      <Sparkles className="text-white w-6 h-6 sm:w-8 sm:h-8" />
                    </div>
                    <h1 className="text-2xl sm:text-3xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-pink-400 to-purple-500">
                      Picture Roulette
                    </h1>
                  </div>
                </div>
              </div>
              

              <div className="p-3 sm:p-6 space-y-4 sm:space-y-8">
                <RecentWinners pastWinning={pastWinning} getNumberColor={getNumberColor} />
                
                <BettingSection
                  betType={betType}
                  setBetType={setBetType}
                  gamePhase={gamePhase}
                  betAmounts={betAmounts}
                  onBet={handleBet}
                  selectedNumber={selectedNumber}
                  setSelectedNumber={setSelectedNumber}
                  spinningIndex={spinningIndex}
                  getNumberColor={getNumberColor}
                />

                <ActiveBets bets={bets} getNumberColor={getNumberColor} />
                
                {error && (
                  <Alert 
                    className="mt-4 bg-red-600/70 backdrop-blur-sm text-white border border-red-500/50"
                  >
                    {error}
                  </Alert>
                )}

                {result !== null && (
                  <Alert 
                    className="mt-4 bg-purple-700/70 backdrop-blur-sm text-white border border-purple-600/50"
                  >
                    <div className="flex items-center gap-2">
                      <span className="font-semibold text-pink-300 text-sm">Winner:</span>
                      <div className={`${getNumberColor(result)} rounded-lg p-1.5 transform hover:scale-105 transition-all w-12 h-12 flex items-center justify-center`}>
                        <div className="w-full h-full relative">
                          <ImageDisplay 
                            id={result} 
                            className="w-full h-full object-contain rounded-lg"
                          />
                        </div>
                      </div>
                    </div>
                  </Alert>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>

      <style jsx global>{`
        .hide-scrollbar {
          -ms-overflow-style: none;
          scrollbar-width: none;
        }
        .hide-scrollbar::-webkit-scrollbar {
          display: none;
        }

        @keyframes glowEffect {
          0% { 
            filter: brightness(1.2);
            box-shadow: 0 0 20px rgba(255, 0, 255, 0.8),
                       0 0 40px rgba(255, 0, 255, 0.4),
                       0 0 60px rgba(255, 0, 255, 0.2);
            transform: scale(1.02);
          }
          50% { 
            filter: brightness(1.5);
            box-shadow: 0 0 30px rgba(255, 255, 0, 0.8),
                       0 0 60px rgba(255, 255, 0, 0.4),
                       0 0 90px rgba(255, 255, 0, 0.2);
            transform: scale(1.04);
          }
          100% { 
            filter: brightness(1.2);
            box-shadow: 0 0 20px rgba(255, 0, 255, 0.8),
                       0 0 40px rgba(255, 0, 255, 0.4),
                       0 0 60px rgba(255, 0, 255, 0.2);
            transform: scale(1.02);
          }
        }

        .glow-animation {
          animation: glowEffect 1s ease-in-out infinite;
          position: relative;
          z-index: 10;
          isolation: isolate;
        }

        @media (orientation: landscape) and (max-height: 640px) {
          .container {
            max-height: 100vh;
            overflow-y: auto;
          }
        }
      `}</style>
    </div>
  );
};
export default MainRoulette;