import axios from "axios"
import { GET_USER_FAILURE, GET_USER_REQUEST, GET_USER_SUCCESS, LOGIN_FAILURE, LOGIN_REQUEST, LOGIN_SUCCESS, LOGOUT, REGISTER_FAILURE, REGISTER_REQUEST, REGISTER_SUCCESS } from "./ActionType"
import { api, API_URL } from "../../Config/Api"

export const registerUser = (reqData) => async (dispatch) => {
    dispatch({ type: REGISTER_REQUEST });
    try {
        const { data } = await axios.post(`${API_URL}/auth/signup`, reqData.userData);
        
        if (data.jwt) {
            localStorage.setItem("jwt", data.jwt);
        }
        
        dispatch({ type: REGISTER_SUCCESS, payload: data.jwt });
        
        reqData.navigate("/");
        
    } catch (error) {
        dispatch({ type: REGISTER_FAILURE, payload: error.response?.data || error.message });
        console.error("Registration error:", error);
    }
};
export const LoginUser = (reqData)=>async(dispatch)=>{
    dispatch({type:LOGIN_REQUEST})
    try {
        const {data} = await axios.post(`${API_URL}/auth/signin`,reqData.userData)
        if(data.jwt)localStorage.setItem("jwt",data.jwt);
        if(data.role==="ROLE_RESTAURENT_OWNER" || data.role==="ROLE_ADMIN"){
            reqData.navigate("/admin/restaurant")
        }
        else{
            reqData.navigate("/")
        }
        dispatch({type:LOGIN_SUCCESS,payload:data.jwt})
        
    } catch (error) {
        dispatch({type:LOGIN_FAILURE,payload:error})
        console.log("error",error)
    }

}

// In GetUser action (Action.js)
export const GetUser = (jwt)=>async(dispatch)=>{
    dispatch({type:GET_USER_REQUEST})
    try {
        const {data} = await api.get(`/api/user/profile`,{
            headers : {
                'Authorization': jwt.startsWith('Bearer ') ? jwt : `Bearer ${jwt}`
            }
        })
        dispatch({type:GET_USER_SUCCESS,payload:data})
        
    } catch (error) {
        console.error("Error response:", error.response?.data);  // Debug log
        dispatch({type:GET_USER_FAILURE,payload:error})
        console.error("error",error)
    }
}

export const logOut = () => {
    console.log("logout");
    return async (dispatch) => {
      dispatch({ type: LOGOUT });
      localStorage.clear();
    };
  };