// BetCard.js
import React from 'react';
import { Coins } from 'lucide-react';

export const BetCard = ({ amount, gamePhase, onBet }) => (
  <button
    className="w-full h-12 rounded-xl flex items-center justify-between px-4
      bg-gradient-to-br from-pink-600/70 to-purple-700/70
      active:scale-95 transition-all duration-150
      disabled:opacity-50 disabled:cursor-not-allowed"
    onClick={() => gamePhase === 'betting' && onBet(amount.value)}
    disabled={gamePhase !== 'betting'}
  >
    <Coins className="w-5 h-5 text-white" />
    <span className="text-base font-bold text-white">{amount.value}</span>
  </button>
);
export default BetCard;