import { api } from "../../Config/Api";
import { ENTER_TOURNAMENT_FAILURE, ENTER_TOURNAMENT_REQUEST, ENTER_TOURNAMENT_SUCCESS, GET_TOURNAMENTS_FAILURE, GET_TOURNAMENTS_REQUEST, GET_TOURNAMENTS_SUCCESS } from "./ActionType";

export const getTournament = (data) => async (dispatch) => {
    dispatch({ type: GET_TOURNAMENTS_REQUEST });    
    try{
        const response = await api.get('/api/tournament/', data, {
            headers: {
                Authorization: `Bearer${localStorage.getItem("jwt")}`
            }
        });
        dispatch({ type: GET_TOURNAMENTS_SUCCESS, payload: response.data });
    } catch (error) {
        dispatch({ type: GET_TOURNAMENTS_FAILURE, payload: error.message });
    }
};
export const enterTournament = (data) => async (dispatch) => {
    dispatch({ type: ENTER_TOURNAMENT_REQUEST });
    try {
        const response = await api.post('/api/tournament/enter', data, {
            headers: {
                Authorization: `Bearer${localStorage.getItem("jwt")}`
            }
        });
        console.log("reeeeeeeeeeeeeewaaaaaaaaaaaaaaa",response);
        dispatch({ type: ENTER_TOURNAMENT_SUCCESS, payload: response.data });
    } catch (error) {
        dispatch({ type: ENTER_TOURNAMENT_FAILURE, payload: error.message });
    }
};