import { Divider, Drawer, useMediaQuery } from '@mui/material'
import React from 'react';
import AccountBalanceWalletIcon from '@mui/icons-material/AccountBalanceWallet';
import LogoutIcon from '@mui/icons-material/Logout';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { logOut } from '../State/Authentication/Action';
import SwitchAccessShortcutAddIcon from '@mui/icons-material/SwitchAccessShortcutAdd';

const menu = [
    {title: "Add-Coins", Icon: <AccountBalanceWalletIcon/>},
    {title: "Withdraw-Money", Icon: <SwitchAccessShortcutAddIcon/>},
    {title: "LogOut", Icon: <LogoutIcon/>},
]

const ProfileNavigation = ({open, handleClose}) => {
    const isSmallScreen = useMediaQuery("(max-width:900px)")
    const navigate = useNavigate();
    const dispach = useDispatch();

    const handleNavigate = (item) => {
        if(item.title === "LogOut") {
            dispach(logOut());
            navigate("/")
        } else {
            navigate(`/my-profile/${item.title.toLowerCase()}`)
        }
        // Close drawer after navigation on mobile
        if (isSmallScreen) {
            handleClose();
        }
    }

    return (
        <div className=''>
            <Drawer 
                variant={isSmallScreen ? "temporary" : "permanent"}
                onClose={handleClose}
                open={isSmallScreen ? open : true}
                anchor='left'
                sx={{
                    zIndex: isSmallScreen ? 1200 : -1,
                    position: "sticky",
                    '& .MuiDrawer-paper': {
                        width: isSmallScreen ? '70vw' : '20vw',
                    }
                }}
            >
                <div className='h-[100vh] flex flex-col justify-center text-xl gap-8 pt-16'>
                    {menu.map((item, i) => (
                        <React.Fragment key={item.title}>
                            <div 
                                onClick={() => handleNavigate(item)} 
                                className='px-5 flex items-center space-x-5 cursor-pointer'
                            >
                                {item.Icon}
                                <span>
                                    {item.title}
                                </span>
                            </div>
                            {i !== menu.length-1 && <Divider/>}
                        </React.Fragment>
                    ))}
                </div>
            </Drawer>
        </div>
    )
}

export default ProfileNavigation