import React from 'react'
import { Card } from '@mui/material'
import { useNavigate } from 'react-router-dom'

const GameCard = ({ game }) => {
  const navigate = useNavigate();

  const handleNavigateToGame = () => {
    console.log(`Navigating to ${game.type}`);
    navigate(`/game/${game.type.toLowerCase()}`);
  }

  return (
    <Card className='w-[18rem] onhover:scale-105'>
      <div className='relative'>
        <img 
          onClick={handleNavigateToGame}
          className='w-full h-[10rem] rounded-t-md object-cover' 
          src={game.image} 
          alt={game.type} 
        />
      </div>
      <div className='p-4 textPart lg:flex w-full justify-between'>
        <div className='space-y-1'>
          <p onClick={handleNavigateToGame} className='font-semibold text-lg cursor-pointer'>
            {game.type}
          </p>
          <p onClick={handleNavigateToGame} className='text-gray-500 text-sm'>
            {game.description}
          </p>
        </div>
      </div>
    </Card>
  )
}

export default GameCard;
