import React from 'react';
import ImageDisplay from './ImageDisplay';

const RecentWinners = ({ pastWinning, getNumberColor }) => (
  <section className="bg-purple-900/40 rounded-xl p-3 border border-purple-700/30 backdrop-blur-sm">
    <div className="flex overflow-x-auto gap-2 pb-2 hide-scrollbar">
      {pastWinning.slice(0, 6).map((number, index) => (
        <div 
          key={index} 
          className={`flex-shrink-0 w-14 h-14 rounded-lg ${getNumberColor(number)}`}
        >
          <ImageDisplay 
            id={number} 
            className="w-full h-full object-contain rounded-lg"
          />
        </div>
      ))}
    </div>
  </section>
);

export default RecentWinners;