import React, { useState } from 'react'
import ProfileNavigation from './ProfileNavigation'
import { Route, Routes } from 'react-router-dom';
import UserProfile from './UserProfile';
import AddCoins from './AddCoins';
import WithdrawCoins from './WithdrawCoins';
import { IconButton } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';

const Profile = () => {
  const [isDrawerOpen, setIsDrawerOpen] = useState(false);

  const handleDrawerClose = () => {
    setIsDrawerOpen(false);
  };

  const handleDrawerOpen = () => {
    setIsDrawerOpen(true);
  };

  return (
    <div className='lg:flex justify-between min-h-screen'>
      {/* Menu button for mobile */}
      <div className='lg:hidden p-4'>
        <IconButton 
          edge="start" 
          color="inherit" 
          aria-label="menu" 
          onClick={handleDrawerOpen}
        >
          <MenuIcon />
        </IconButton>
      </div>

      <div className='lg:sticky lg:h-screen lg:w-[20%]'>
        <ProfileNavigation 
          open={isDrawerOpen} 
          handleClose={handleDrawerClose} 
        />
      </div>

      <div className='lg:w-[80%]'>
        <Routes>
          <Route path='/' element={<UserProfile/>}/>
          <Route path='/account/profile/*' element={<UserProfile/>}/>
          <Route path='/my-profile' element={<UserProfile/>}/>
          <Route path='/add-coins' element={<AddCoins/>}/>
          <Route path='/Withdraw-Money' element={<WithdrawCoins/>}/>
        </Routes>
      </div>
    </div>
  )
}
export default Profile;