import React from 'react';
import { Timer } from 'lucide-react';

const GameTimer = ({ gamePhase, timeLeft, isSpinning }) => {
  const getPhaseText = () => {
    switch (gamePhase) {
      case 'betting':
        return 'Betting Phase';
      case 'spinning':
        return isSpinning ? 'Spinning...' : 'Preparing to spin...';
      case 'result':
        return 'Next game in';
      default:
        return '';
    }
  };

  return (
    <div className="fixed top-20 right-4 z-50">
      <div className="flex items-center gap-2 bg-gradient-to-br from-purple-600/90 to-pink-600/90 
        rounded-full px-4 py-2 shadow-lg border border-purple-400/30 backdrop-blur-sm
        animate-float transform hover:scale-105 transition-all duration-300">
        <Timer className="w-5 h-5 text-purple-200" />
        <div className="text-purple-100 font-medium">
          <span className="mr-2">{getPhaseText()}</span>
          {timeLeft > 0 && (
            <span className="font-bold text-white">
              {timeLeft}s
            </span>
          )}
        </div>
      </div>
    </div>
  );
};

// Add the floating animation
const styles = `
  @keyframes float {
    0% {
      transform: translateY(0px);
    }
    50% {
      transform: translateY(-10px);
    }
    100% {
      transform: translateY(0px);
    }
  }

  .animate-float {
    animation: float 3s ease-in-out infinite;
  }
`;

// Add the styles to the document
const styleSheet = document.createElement("style");
styleSheet.innerText = styles;
document.head.appendChild(styleSheet);

export default GameTimer;