import { Select, MenuItem, FormControl, InputLabel } from '@mui/material';
import React from 'react';

export const BettingControls = ({ betType, setBetType, gamePhase }) => {
  return (
    <div className="flex flex-col gap-3 w-full">
      <FormControl fullWidth>
        <InputLabel id="bet-type-label">Select bet type</InputLabel>
        <Select
          labelId="bet-type-label"
          value={betType}
          onChange={(e) => setBetType(e.target.value)}
          disabled={gamePhase !== 'betting'}
          className="w-full h-12 bg-purple-800/50 backdrop-blur-sm text-white border-purple-600/50"
        >
          <MenuItem value="NUMBER">Single Picture</MenuItem>
          <MenuItem value="RED">Red</MenuItem>
          <MenuItem value="BLACK">Black</MenuItem>
          <MenuItem value="EVEN">Even</MenuItem>
          <MenuItem value="ODD">Odd</MenuItem>
        </Select>
      </FormControl>
    </div>
  );
};