import { persistStore, persistReducer } from 'redux-persist';
import storage from 'redux-persist/lib/storage';
import { legacy_createStore, applyMiddleware } from 'redux';
import { thunk } from 'redux-thunk';
import { combineReducers } from 'redux';
import { authReducer } from './Authentication/Reducer';
import { coinReducer } from './Coin/Reducer';
import { tournamentReducer } from './Tournament/Reducer';
import { SoratReducer } from './Sorat/Reducer';
import { RouletteReducer } from './Roulette/Reducer';

// Persist configuration
const persistConfig = {
  key: 'root',
  storage,
  whitelist: ['auth', 'coins','roulette'], // Optional: Specify which reducers to persist
};

// Root reducer
const rootReducer = combineReducers({
  auth: authReducer,
  coins: coinReducer,
  tournament: tournamentReducer,
  sorat: SoratReducer,
  roulette:RouletteReducer
});

// Persisted reducer
const persistedReducer = persistReducer(persistConfig, rootReducer);

// Create store and persistor
export const Store = legacy_createStore(persistedReducer, applyMiddleware(thunk));
export const persistor = persistStore(Store); // Export persistor