import React from 'react'
import './Home.css'
import GameCard from '../Game/GameCard'
const Home = () => {
  const games = [
    {
      type: "sorat",
      image: "https://i0.wp.com/gebogame.com/wp-content/uploads/2023/12/Chhatri-Wala-Game-1.jpg?fit=1024,576&ssl=1",
      description: "Experience the thrill of the casino from anywhere! Our online roulette game offers exciting betting options and real-time gameplay. Place your bets on numbers, colors, or combinations and watch as the wheel determines your fate. With fair odds and instant payouts, every spin is a new opportunity to win big!"
    }
  ];
  return (
        <div>
          <div className="banner"></div>
         <section className='px-5 lg:px-20 pt-10'>
            <h1 className='text-2xl font-semibold text-gray-400 pb-8'>Order From Our Handpicked Favorites</h1>
            <div className='flex flex-wrap items-center justify-around gap-5 onhover:scale-105'>
            {games.map((game, index) => (
            <GameCard key={index} game={game} />
          ))}
            </div>
        </section>
        </div>
  )
}

export default Home