import { Alert, Button, Input } from '@mui/material';
import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { enterTournament } from '../State/Tournament/Action';
export const TournamentEntry = () => {
  const [betAmount, setBetAmount] = useState('');
  const [isSubmitted, setIsSubmitted] = useState(false); // new state variable
  const [error, setError] = useState(null); // new state variable to store error message
  const dispatch = useDispatch();
  const { auth, tournament, coins } = useSelector(Store => Store);

  console.log("Tournament Entry", tournament);

  const handleEnterTournament = async (e) => {
    e.preventDefault();
    if (!betAmount || isNaN(betAmount) || betAmount <= 0) {
      setError('Invalid bet amount');
      return;
    }

    try{
     dispatch(enterTournament({email: auth.user.email,betAmount: parseFloat(betAmount)}));
      console.log("ppkkpkpkpkp",tournament.tournament);
      const data = tournament.tournament;
      console.log("llllll",data);
      setIsSubmitted(true);
      setBetAmount('');}
    catch(error){
      setError(error.message);
    }
    }; 

  return (
    <div className="w-full max-w-md p-4 space-y-4">
      <div className="text-2xl font-semibold mb-4">Tournament Entry</div>
      <form onSubmit={handleEnterTournament} className="space-y-4">
        <div>
          <label className="block text-sm font-medium mb-1">
            Bet Amount (Coins)
          </label>
          <Input
            type="number"
            value={betAmount}
            onChange={(e) => setBetAmount(e.target.value)}
            placeholder="Enter bet amount"
            className="w-full"
          />
        </div>

        <Button type="submit" className="w-full">
          Enter Tournament
        </Button>
      </form>

      { (isSubmitted || error) &&(
        <Alert severity={error || tournament?.error ? 'error' : 'success'}>
          {error || tournament?.error ? (error ? error : tournament?.error): (<a href={tournament?.tournament?.tournamentLink}>{tournament?.tournament?.tournamentLink}</a>)}
          
        </Alert>
      )}
    </div>
  );
};
export default TournamentEntry;